@import "normalizes.scss";
@import "resets.scss";

* {
    box-sizing: border-box; }

$color-blue: #4B9EFF;
$color-orange: #e7b28c;

body {
    position: relative;
    overflow-x: hidden;
    font-family: sans-serif; }

.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
    &__ul {
        display: flex;
        gap: 50px; }
    &__li {
        text-decoration: none;
        color: rgb(0, 0, 0);
        font-size: 14px;
        font-weight: 400;
        line-height: 24px; }
    &__link {
        text-decoration: none;
        color: rgb(0, 0, 0);
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        &:hover {
            text-decoration: underline; } }
    &__form {
        display: flex;
        align-items: center;
        gap: 10px; }
    &__select {
        color: rgb(0, 0, 0);
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        border: 1px solid #f58c4e;
        padding: 2px 20px;
        border-radius: 5px; }
    &__btn {
        color: rgb(0, 0, 0);
        background: #f58c4e;
        border: none;
        border-radius: 5px;
        padding: 2px 20px;
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        transition: .3s all;
        &:hover {
            background: #f3a070;
            cursor: pointer; }
        &:active {
            background: #d3763f; } }
    &__hr {
        color: #000;
        margin-top: 20px; } }

.wrapper-base {
    padding-top: 50px;
    min-height: calc(90vh - 100px); }

.footer {
    display: flex;
    align-items: center;
    padding-bottom: 50px;
    &__hr {
        margin-bottom: 50px; }
    &-logo {
        margin-right: 100px; }
    &__li {
        margin-bottom: 10px; }
    &__link {
        text-decoration: none;
        color: rgb(0, 0, 0);
        font-size: 20px;
        font-weight: 400;
        line-height: 24px;
        &:hover {
            text-decoration: underline; } }
    &__text {
        color: rgb(0, 0, 0);
        font-size: 14px;
        font-weight: 400;
        text-align: center;
        padding-bottom: 50px; } }

.main {
    &__title {
        color: rgb(26, 18, 11);
        font-size: 40px;
        font-weight: 500;
        line-height: 54px;
        margin-bottom: 20px; }
    &__post-li {
        margin-bottom: 30px; }
    &__content {
        margin-top: 10px; } }



.about-section {
    min-height: 430px;
    margin-bottom: 70px;
    &__title {
        color: rgb(26, 18, 11);
        font-size: 40px;
        font-weight: 500;
        line-height: 54px;
        text-align: center;
        margin-bottom: 43px; }
    &__text {
        color: rgb(26, 18, 11);
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        letter-spacing: 3%;
        margin-right: 105px; }
    &__wrapper {
        display: flex;
        align-items: center; }
    &__img {
        width: 550px; } }

.aboutcom-section {
    min-height: 130px;
    margin-bottom: 70px;
    &__title {
        color: rgb(26, 18, 11);
        font-size: 40px;
        font-weight: 500;
        line-height: 54px;
        text-align: center;
        margin-bottom: 43px; }
    &__text {
        color: rgb(26, 18, 11);
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        letter-spacing: 3%;
        text-align: center; } }

#worker-margin {
    margin-bottom: 15px; }

.aboutrec-section {
    min-height: 130px;
    margin-bottom: 70px;
    &__title {
        color: rgb(26, 18, 11);
        font-size: 40px;
        font-weight: 500;
        line-height: 54px;
        margin-bottom: 50px;
        text-align: center; }
    &__wrapper {
        display: flex;
        align-items: center;
        margin-bottom: 50px; }
    &__wrapper-left {
        margin-right: 85px; }
    &__text {
        color: rgb(26, 18, 11);
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        letter-spacing: 3%;
        text-align: center;
        max-width: 790px;
        border: 4px solid rgb(219, 174, 139);
        border-radius: 37px; }
    &__padding1 {
        padding: 54px 40px; }
    &__padding2 {
        padding: 54px 40px; }
    &__wrapper-req {
        border: 4px solid rgb(219, 174, 139);
        border-radius: 37px; }
    &__text-req {
        padding: 35px 13px 35px 42px; }
    &__title-mini {
        color: rgb(26, 18, 11);
        font-size: 28px;
        font-weight: 700;
        line-height: 34px;
        letter-spacing: 3%;
        margin-bottom: 14px; }
    &__descr {
        color: rgb(26, 18, 11);
        font-size: 28px;
        font-weight: 500;
        line-height: 34px;
        letter-spacing: 3%; } }

#aboutrec-section__span {
    font-weight: 600; }




